    background-color: #ddd;
    padding: 7px;
    border-radius: 6px;
    border: 0;
    box-sizing: border-box;
    color: black;
    cursor: pointer;
    font-size: 18px;
    height: 50px;
    margin-top: 38px;
    text-align: center;
    width: 100%;
}


/* Change the link color to #111 (black) on hover */

.naav:hover {
    background-color: #111;
}

.navbar {
    /* display: flex; */
    flex: 1;
    width: 100%;
    float: left;
    /* justify-content: space-evenly; */
}


/* Change the color of links on hover */

.navbar a:hover {
    background-color: #ddd;
    color: black;
}


/* Add a color to the active/current link */

.navbar a.active {
    background-color: #08d;
    color: white;
}